.product {
  @include container(1170px, 30px);

  &__title {
    @include pm-zero();
    margin-top: 89px;
    margin-bottom: 51px;

    font-family: "Roboto Condensed", "Arial Narrow", sans-serif;

    @include font(36px, 400, 51px);
    color: $text-color;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    @include pm-zero();
    padding-bottom: 99px;

    list-style: none;
  }

  &__item {
    margin-bottom: 30px;
  }
}

@media (max-width: $table-width) {
  .product {
    padding-left: 25px;
    padding-right: 25px;

    &__title {
      max-width: 85%;
      margin-top: 50px;
      margin-bottom: 49px;

      font-size: 32px;
      line-height: 45px;
    }

    &__list {
      flex-direction: column;

      padding-bottom: 36px;
    }

    &__item {
      margin-bottom: 43px;
    }
  }
}

@media (max-width: $mobile-width) {
  .product {
    padding-left: 15px;
    padding-right: 15px;

    &__list {
      flex-direction: row;

      max-width: 609px;
      margin: 0 auto;
    }

    &__title {
      max-width: 100%;
      margin-top: 48px;
      margin-bottom: 41px;
      margin-left: 10px;

      font-size: 28px;
      line-height: 40px;
    }

    &__item {
      margin-bottom: 29px;
    }
  }
}

@media (max-width: $only-mobile) {
  .product__item {
    margin-right: auto;
    margin-left: auto;
  }
}
