@mixin font($size, $weight, $line-height) {
  font-size: $size;
  font-weight: $weight;
  line-height: $line-height;
}

@mixin container($max-width, $padding) {
  margin: 0 auto;
  max-width: $max-width;
  padding-left: $padding;
  padding-right: $padding;
}

@mixin pm-zero($margin: 0, $padding: 0) {
  margin: $margin;
  padding: $padding;
}

@mixin button($width: 180px, $min-height: 45px) {
  box-sizing: border-box;
  width: $width;
  min-height: $min-height;
  padding-left: 20px;
  padding-right: 20px;
  border: 1px solid #ffffff;
  border-radius: 5px;
  text-align: center;
  font-family: "Roboto Condensed", "Arial Narrow", sans-serif;

  @include font(14px, 700, 16px);
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #ffffff;
  background-color: transparent;
}

@mixin bg-img($url) {
  background-image: url($url);
  background-repeat: no-repeat;
  background-position: 0 0;
}
